import React, { useState } from 'react';
import { observer } from "mobx-react";

import './ReceiptCard.css';

const ReceiptCard = observer((props) => {

    return (
      <div className="receipt-card-container">
        
        <div className="receipt-card-header">
          <div>
            <label htmlFor={"receiptNumber-" + props.index}>Numero</label>
            <input 
              type="text" 
              name={"receiptNumber-" + props.index} 
              placeholder="" 
              id={"receiptNumber-" + props.index}
              />
          </div>
          
          <div>
            <label htmlFor={"paidValue-" + props.index}>Valor</label>
            <input 
              type="text"   
              name={"paidValue-" + props.index} 
              placeholder="" 
              id={"paidValue-" + props.index}
              value={props.receipt.paidValue} onChange={(e) => props.receipt.paidValue = e.target.value}/>
          </div>
        </div>

        <div className="receipt-card-payer">
          <label htmlFor={"payer-" + props.index}>Recebi (emos) de</label>
          <input 
            className="u-full-width" 
            type="text"   
            name={"payer-" + props.index} 
            placeholder="" 
            id={"payer-" + props.index}
            value={props.receipt.payer} onChange={(e) => props.receipt.payer = e.target.value}/>

          <label htmlFor={"referrerTo-" + props.index }>Referente</label>
          <input 
            className="u-full-width" 
            type="text"   
            name={"referrerTo-" + props.index } 
            placeholder="" 
            id={"referrerTo-" + props.index }
            value={props.receipt.referrerTo} onChange={(e) => props.receipt.referrerTo = e.target.value}/>
        </div>
        
        <div className="receipt-card-actions">
          <button className="button no-printable">Imprimir</button>
          <a className="button  no-printable" href={"#receipt/" + props.index}>Editar</a>
          <button className="button no-printable" onClick={() => props.removeReceipt(props.index)}>Remover</button>
        </div>
      </div>
    );
  }
)
export default ReceiptCard;